.footer {
  width: 100%;
  max-width: 880px;
}

@media screen and (max-width: 1100px) {
  .footer {
    max-width: 700px;
  }
}

@media screen and (max-width: 850px) {
  .footer {
    max-width: 545px;
  }
}

@media screen and (max-width: 600px) {
  .footer {
    max-width: 282px;
  }
}